.container {
	margin-right: 10px;

	.cart {
		position: relative;

		.count {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 9px;
			left: 12px;

			width: 17px;
			height: 17px;
			background-color: rgba(255, 200, 94, 1);
			border: 2px solid #fff;
			border-radius: 50%;

			line-height: 12px;
			color: #fff;
			font-weight: 700;
			font-size: 10px;
		}
	}
}
